import { Pipe, PipeTransform } from '@angular/core';
import { RxDefinitionService } from './definition.service';
import { IBundleDescriptor } from '../bundle/bundle.interfaces';

@Pipe({
  name: 'rxDefinitionScopePipe'
})
export class RxDefinitionScopePipe implements PipeTransform {
  constructor(private rxDefinitionService: RxDefinitionService) {}

  transform(value: string, bundleDescriptor: IBundleDescriptor): string {
    return this.rxDefinitionService.getScopeName(value, bundleDescriptor);
  }
}
